<template>
    <div class="game-page-box" id="jumpone-page-box">
    </div>
</template>

<script>

    import {browser,sdk} from "../../common/util/bitUtil";
    import gameInit from './js/main'

    export default {
        name: 'jumpone',
        /**
         * 组件的初始数据
         */
        data() {
            return {}

        },
        computed: {},
        components: {},

        /**
         * 组件的方法列表
         */
        methods: {},
        mounted() {
            // game.state.start('boot');
            let query = this.$route.query || {}

            // game.state.start('boot');
            const gameInstance = gameInit({
                width: window.innerWidth,
                height: window.innerHeight,
                id: 'jumpone-page-box'
            })
            if(browser.app){
                sdk.onAdCompletion(function(params){
                    console.log(params)
                })

                gameInstance.hooks.start = function () {
                    sdk.gameShowAd(query)
                }
            }
        }
    }
</script>
